@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:400,400i);
:root {
  --lt-pink: #FFEBFF;
  --med-pink: #D6B4D6;
  --bg-navy: #1B182E;
}

* {
  box-sizing: border-box;
}

html, body {
  background: #1B182E;
  background: var(--bg-navy);
  margin: 0 auto;
  height: 100%;
  font-family: "Roboto Mono", monospace;
  color: #D6B4D6;
  color: var(--med-pink);
}

body {
  position: relative;
}

p {
  color: #D6B4D6;
  color: var(--med-pink);
  font-size: 14px;
}

.page-container {
  position: absolute;
  left: 70px;
  top: 70px;
  width: calc(100% - 140px);
  height: calc(100% - 140px);
}

#canvas {
  position: absolute;
}
.nav {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 64px;
}

span#name-label {
  position: absolute;
  left: 0;
}

.nav-items ul {     
  margin: 0;
  padding: 0;
}

.nav-items li {
  list-style-type: none;
  display: inline-block;
  position: relative;
  margin-left: 24px;
  margin-right: 24px;
}
 
a, a:visited { 
  text-decoration: none;
  color: var(--med-pink);
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 1.5px;
}

.underline:hover:after,
.underline:focus:after,
.underline:active:after {
  width: 100%;
}

.underline:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -3px;
  height: 1px;
  background: var(--lt-pink);
  width: 0;
  -webkit-transition: width 0.25s;
  transition: width 0.25s;
}
#info {
  max-width: 450px;
  line-height: 22px;
  position: absolute;
  bottom: 0;
}

.borderline {
  position: relative;
  height: 2px;
  margin-bottom: 24px;
  background: var(--med-pink);
}

span#info-label {
  color: var(--lt-pink);
  letter-spacing: 1.2px;
  font-size: 22px;
}

div#info-content {
  margin-top: 24px;
  margin-bottom: 28px;
}

img#ig-logo {
  height: 22px;
  width: 22px;
  margin-right: 16px;
}

img#email-logo {
  height: 22px;
}
.inner-page-container {
  margin-bottom: 70px;
  overflow-x: auto;
}

.page-header {
  position: relative;
}

h1.page-header-label {
  font-size: 24px;
  letter-spacing: 1.5px;
  color: var(--lt-pink);
  display: inline-block;
}

div.page-header-divider {
  display: inline-block;
	height: 2px;
	background: var(--lt-pink);
  width: 48px;
  left: 128px;
	position: absolute;
	top: 50%;
}

div.page-content {
  line-height: 26px;
  max-width: 550px;
  margin-left: 128px;
}
